@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: -webkit-linear-gradient(
        315deg,
        rgba(179, 229, 252, 1) 0%,
        rgba(179, 229, 252, 1) 50%,
        rgba(240, 98, 146, 1) 50%,
        rgba(240, 98, 146, 1) 100%
  );
    background: linear-gradient(
        135deg,
        rgba(179, 229, 252, 1) 0%,
        rgba(179, 229, 252, 1) 50%,
        rgba(240, 98, 146, 1) 50%,
        rgba(240, 98, 146, 1) 100%
  );
  font-family: 'Rubik', sans-serif;
  color: #78909c;
}
.JokeList{
    display: flex;
    height: 80%;
    width: 80%;
}

.JokeList-sidebar{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    background-color: #9575cd;
    width: 30%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1),
    inset 0 0 25px #7e57c2;
    z-index: 2;
}

.JokeList-title{
    font-size: 4rem;
    margin: 2rem;
    color: white;
    font-weight: 300;
    letter-spacing: 0.6rem;
}

.JokeList-title span{
    font-weight: 700;
}

.JokeList-sidebar img{
    width: 50%;
    border-radius: 50%;
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokeList-sidebar img:hover {
    -webkit-animation-name: shake-slow;
            animation-name: shake-slow;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
  }

.JokeList-jokes{
  height: 90%;
  background: white;
  align-self: center;
  width: 70%;
  overflow-y: scroll;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
}

.JokeList-getmore {
    font-size: 1.5rem;
    width: 50%;
    border-radius: 2rem;
    padding: 1rem;
    color: white;
    font-weight: 700;
    margin: 2rem;
    border: none;
    word-spacing: 0.2rem;
    cursor: pointer;
    -webkit-transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
    transition: 0.8s cubic-bezier(0.2, 1, 0.2, 1);
    outline: none;
    background: -webkit-linear-gradient(
      315deg,
      rgba(179, 229, 252, 1) 0%,
      rgba(179, 229, 252, 1) 50%,
      rgba(240, 98, 146, 1) 50%,
      rgba(240, 98, 146, 1) 100%
    );
    background: linear-gradient(
      135deg,
      rgba(179, 229, 252, 1) 0%,
      rgba(179, 229, 252, 1) 50%,
      rgba(240, 98, 146, 1) 50%,
      rgba(240, 98, 146, 1) 100%
    );
    box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  }

  .JokeList-getmore:hover {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.25);
  }
  
  .JokeList-spinner{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-top: 15%;
    font-size: 6em;
    color: rgba(240, 98, 146, 1) 
  }

  .JokeList-loading-title{
    margin-left: 1.1rem;
    color: #9575cd;
    font-size: 1em;
  }

::-webkit-scrollbar{
    width: 10px;
}

::-webkit-scrollbar-thumb{
    background-color: #9575cd;
}

::-moz-selection{
    background-color: #f06292;
    color: white;
}

::selection{
    background-color: #f06292;
    color: white;
}

@-webkit-keyframes shake-slow {
    0% {
      -webkit-transform: translate(0px, 0px) rotate(0deg);
              transform: translate(0px, 0px) rotate(0deg);
    }
    2% {
      -webkit-transform: translate(5px, -6px) rotate(3.5deg);
              transform: translate(5px, -6px) rotate(3.5deg);
    }
