.Joke{
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid #eeeeee;
    font-weight: 400;
    padding: 1rem;
}

.Joke-buttons{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1rem;
    width: 15%;
}

.Joke-text{
    width: 75%;
    font-size: 1.2rem;  
}

.Joke-votes{
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    text-align: center;
    font-size: 1.1rem;
    font-weight: 300;
    border: 3px solid rgb(230, 79, 79);
    box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}

.fa-arrow-up,
.fa-arrow-down {
  font-size: 1.1em;
  margin: 5px;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.fa-arrow-up:hover {
    -webkit-animation-name: move-up;
            animation-name: move-up;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    color: #81c92f;
  }
  
  @-webkit-keyframes move-up {
    0%,
    50%,
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25%,
    75% {
      -webkit-transform: translateY(-3px);
              transform: translateY(-3px);
    }
  }
  
  @keyframes move-up {
    0%,
    50%,
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25%,
    75% {
      -webkit-transform: translateY(-3px);
              transform: translateY(-3px);
    }
  }
  .fa-arrow-down:hover {
    -webkit-animation-name: move-down;
            animation-name: move-down;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    color: #f44336;
  }
  @-webkit-keyframes move-down {
    0%,
    50%,
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25%,
    75% {
      -webkit-transform: translateY(3px);
              transform: translateY(3px);
    }
  }
  @keyframes move-down {
    0%,
    50%,
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
    25%,
    75% {
      -webkit-transform: translateY(3px);
              transform: translateY(3px);
    }
  }

  .Joke-emoji{
    font-size: 2rem;
    margin-left: auto;
    border-radius: 50%;
    box-shadow: 0 10px 38px rgba(0, 0, 0, 0.2), 0 10px 12px rgba(0, 0, 0, 0.1);
}
  
  .Joke-emoji:hover {
    -webkit-animation-name: spin;
            animation-name: spin;
    -webkit-animation-duration: 0.7s;
            animation-duration: 0.7s;
